@use "../../styles/variables"as *;

.dropdown {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    
    box-sizing: border-box;

    height: fit-content;
    width: 100%;
    
    border-radius: 5px;

    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;

        box-sizing: border-box;

        height: 30px;
        width: 100%;

        padding: 10px;

        border-radius: 5px;

        background: $primaryColor;
        color: $white;

        @media screen and (min-width: 768px) {
            height: fit-content;
            padding: 9px 15px;
            border-radius: 10px;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        flex-basis: auto;

        box-sizing: border-box;

        width: 100%;
        gap: 2px;
        padding: 10px;

        background: $grey;

        @media screen and (min-width: 768px) {
            padding: 25px 28px 20px 20px;
        }

        &-active {
            height: auto;
            max-height: 9999px;
            transition: al 0.3s cubic-bezier(0, 1, 0, 1);
        }
    }

    .toggleButton {
        transform: rotate(0deg);
        transition: transform 0.2s;
        cursor: pointer;

        &__active {
            transform: rotate(180deg);
        }

        @media screen and (min-width: 768px) {
            height: 32px;
            width: 32px;
        }
    }
}